import React, {useState} from 'react'
import './ShoppingList.scss'
import PlantItem from '../plant-item/PlantItem'
import plantList from '../../data/plantList'
import PlantDetail from '../plant-detail/PlantDetail'

function Sort({setShoppingList}) {

    function handleChange(e) {

        e.target.value !== "all"
            ? setShoppingList(plantList.filter((plant) => plant.category === e.target.value))
            : setShoppingList(plantList)
    }
    return (
        <form onChange={handleChange}>
                    <div className="radio">
                        <input type="radio" name="sort" defaultChecked={true} value="all" id="all"/>
                        <label htmlFor="all">All</label>
                    </div>
                    <div className="radio">
                        <input type="radio" name="sort" value="easycare" id="easycare"/>
                        <label htmlFor="easycare">Easy Care</label>
                    </div>
                    <div className="radio">
                        <input type="radio" name="sort" value="shadow" id="shadow"/>
                        <label htmlFor="shadow">Shadow</label>
                    </div>
                    <div className="radio">
                        <input type="radio" name="sort" value="petfriendly" id="petfriendly"/>
                        <label htmlFor="petfriendly">Pet Friendly</label>
                    </div>
                </form>
    )
}

function ShoppingList({cart, setCart, setIsAdded, isPlp, setIsPlp}) {

    const [isOpen] = useState(true)

    const [toShow,
        setToShow] = useState([])

        const [shoppingList,
            setShoppingList] = useState(plantList)

    function addToCart(id, name, price) {
        const plantAdded = cart.find((plant) => plant.name === name)

        if (plantAdded) {
            const filteredPlantAdded = cart.filter((plant) => plant.name !== name);
            setCart([
                {
                    id,
                    name,
                    price,
                    amount: plantAdded.amount + 1
                },
                ...filteredPlantAdded
            ]);
            setIsAdded(true)
        } else {
            setCart([
                {
                    id,
                    name,
                    price,
                    amount: 1
                },
                ...cart
            ]);
            setIsAdded(true)
        }
    }

    function openDetail(id, image, name, price, offer, light, water) {

        window.scrollTo(0, 0)
        const plantToShow = shoppingList.filter((plant) => plant.id === id)

        if (plantToShow) {
            setToShow([
                {
                    id,
                    image,
                    name,
                    price,
                    offer,
                    light,
                    water
                }
            ])
            setIsPlp(false)
        }
    }

    

    return (isPlp
        ? <div className="ShoppingContainer">
                <Sort shoppingList={shoppingList} setShoppingList={setShoppingList} />
                <ul className="ShoppingList">
                    {shoppingList.map(({
                        id,
                        name,
                        image,
                        isSpecialOffer,
                        light,
                        water,
                        price
                    }) => <PlantItem
                        key={id}
                        cover={image}
                        name={name}
                        price={price}
                        offer={isSpecialOffer}
                        light={light}
                        water={water}
                        isOpen={isOpen}
                        openPanel={() => openDetail(id, image, name, price, isSpecialOffer, light, water)}/>)}
                </ul>
            </div>
        : <div className="ShoppingContainer">{toShow.map(({
                id,
                image,
                name,
                price,
                isSpecialOffer,
                light,
                water
            }) => <PlantDetail
                key={id}
                cover={image}
                name={name}
                price={price}
                isSpecialOffer={isSpecialOffer}
                light={light}
                water={water}
                isOpen={isOpen}
                onClick={() => addToCart(id, name, price, image)}
                closePanel={() => setIsPlp(true)}/>)}</div>)
}

export default ShoppingList